/*
 *
 * asheville
 *
 */
@import 'shared';

// mixin to extend asheville filter
// @mixin asheville
// @param $filters... {filter} - Zero to many css filters to be added
// @example
//   img {
//     @include asheville;
//   }
//   or
//   img {
//     @include asheville(blur(2px));
//   }
//   or
//   img {
//     @include asheville(blur(2px)) {
//       /*...*/
//     };
//   }
@mixin asheville($filters...) {
  @include filter-base;
  filter: contrast(.8) brightness(1.1) blur(2px) $filters;

  img {
    filter:grayscale(1);
    opacity: .5;
  }

  &::after {
    background: linear-gradient(45deg, rgba(#C3C2FB, 1), rgba(#a6c1ee, 1));
    mix-blend-mode: hard-light;
    opacity: 1;
  }

  @content;
}

// asheville Instagram filter
%asheville,
.asheville {
  @include asheville;
}
